<template>
<div>
  <div class="foot-wrapper">
    <span class="button-bet" @click="ShowKeyb"></span>
    <input class="chat-input" type="text" name="" @keyup.enter="send" v-model="msg" @change="postMsg">
    <span class="chat-sub" :class="{'primary':!!msg}"  @click.self="send"></span>
  </div>
</div>
</template>

<script>
import {CHAT} from '../../assets/client'
import store from '../../store/index.js'
import { mapState ,mapMutations } from 'vuex'
import  md5 from 'js-md5';
export default {
  data () {
    return {
      msg:''
    }
  },
  mounted(){
   
  },
  computed:{
    // 
    ...mapState([
      'message_input',
      'message_bet',
      'bac_num',
      'msg_type',
      'msg_admin',
      'ShowKeyboard',
      'selectRoom',
      'userInfo',
      'cancalMessage'
    ])
  },
  watch:{
    message_input(val){
      this.msg = val
    },
  },
  methods:{
    ...mapMutations([
      'merge'
    ]),
    postMsg(){
      var vm = this
      vm.merge({
        message_input:vm.msg,//
      })
    },
    ShowKeyb(){
      var vm = this
      let ShowKeyboard = !vm.ShowKeyboard
      vm.merge({
        ShowKeyboard:ShowKeyboard
      })
    },
    send(){
      var vm = this
       if(vm.cancalMessage){
        vm.$toast('')
        return false
      }
      if(vm.message_input == ''){
        vm.$toast('')
        return false
      }
      const timestamp = Date.parse(new Date()); 
      let time = md5(vm.userInfo.username+timestamp+'')
      let msg_type = 0
      /**
       * msg_type:
    msg_type: 0 
    msg_type: 1 
    msg_type: 2 
    msg_type: 5   ()
    msg_type: 6 showbacc
    msg_type: 7 jiesuan 
    "msg_admin": 100 is_start
    "msg_admin": 200 is_stop
    "msg_admin": 300 is_show
    "msg_admin": 400 is_jiesuan 
    msg_admin 100  webmsg_type2 web2, .
 msg_admin 200   msg_type 0 0 1 !
300400 
       * **/
      if(vm.msg_type == 3){
        if(vm.msg_admin == 100){
          msg_type =2
        }else if(vm.msg_admin == 200){
          msg_type =0
        }
      }
     
      // msg_type =2
      let msg = vm.message_bet+vm.message_input
      let formData = {
        msg_type: msg_type,
        msg_id: time,
        from_username: vm.userInfo.username,
        from_nickname:vm.userInfo.nickname,
        to_nickname:"",
        is_gruop: 'true',
        room_name: vm.selectRoom,
        avatal_url: vm.userInfo.avatar_url,
        image_url: "",
        msg_admin: '',
        bac_num:vm.bac_num,
        content:msg
      }
       console.log(formData)
      CHAT.submit(formData)
      vm.msg = ''
      vm.merge({
        message_input:''
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.foot-wrapper{
  background-color: #ece7e7;
  height: 65px;
  flex-shrink: 0;
  display: flex;
  padding: 10px 10px 12px;
  justify-content: space-between;
  border-top: solid 1px rgba(0,0,0,0.1);
  .button-bet{
    width: 2rem;
    height: 2rem;
    margin-top: .2rem;
    background-image:url('../images/keyboard.png'); 
    background-size:100% 100%;
    margin-right: .5rem; 
  }
  .chat-input{
    height: 42px;
    width: ~'calc(75% - 2.5rem)';
    border-radius: 5px;
    outline: none;
    border:none; 
    box-sizing: border-box;
    padding: 5px;
  }
  .chat-sub{
    display: flex;
    background-color: rgba(0,0,0,0.2);
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 18%;
        border-radius: 5px;
    outline: none;
    color: #666;
  }
  .primary{
    background-color: #007afc;
    color: #fff;
  }
}
</style>


