import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'

Vue.use(Vuex)

const state = {
  roomList: [],//
  selectRoom:'',//
  userInfo:{},//
  msg_admin:0,//
  msg_type:0,//msg_type : 3 
  ShowKeyboard:false,
  
  
  message_input:'',//
  message_bet:'',//
  bac_num:'',// 12xue3ju
  runing:false,//zhu

  roadMapHistory:'',//xue
  tongji_ws:'',//
  logout:false,//
  cancalMessage:false,// 

  money:0,//
}

export default new Vuex.Store({
  state,
  actions,
  mutations: {
    ['merge'](state, payload) {
      Object.assign(state, payload);
    },
  },
})



